.MuiPopover-paper {
  margin-left: 1rem !important;
  padding: 8px !important;
  border-radius: 10px !important;
  background-color: var(--popover-background) !important;
  border: 1px solid var(--popover-border) !important;
  box-shadow: var(--popover-shadow) 0px 4px 20px !important;
}

.popover-list .item {
  display: grid;
  grid-template-rows: repeat(2, 1fr);
  grid-column-gap: 0.45rem;
  grid-row-gap: 0;
  align-items: center;
  width: 100%;
  padding: 0.4rem 1rem 0.4rem 0.6rem;
  border-radius: 6px;
  cursor: pointer;
}

.popover-list .item.lng {
  grid-template-columns: 40px 1fr;
}

.popover-list .item.theme {
  grid-template-columns: 32px 1fr;
}

.popover-list .item:not(:first-child) {
  margin-top: 0.25rem;
}

.popover-list .item.select {
  background-color: var(--popover-background-select);
}

.popover-list .item:hover:not(.select) {
  background-color: var(--popover-background-active);
}

.popover-list .item svg {
  grid-area: 1 / 1 / 3 / 2;
  color: var(--color-primary);
}

.popover-list .item .name {
  grid-area: 1 / 2 / 2 / 3;
  font-size: 0.905rem;
  letter-spacing: 0.0387rem;
  line-height: 1rem;
  font-weight: 400;
  color: var(--popover-color);
}

.popover-list .item .alt {
  grid-area: 2 / 2 / 3 / 3;
  font-size: 0.85rem;
  letter-spacing: 0.0187rem;
  line-height: 1rem;
  font-weight: 300;
  color: var(--popover-color-alt);
}
